.badges-group {
  display: inline-flex;
  flex-wrap: wrap;
  align-items: center;
  border-radius: 50px;
  padding: 5px 8px;
  margin-bottom: 10px;
}
.badges-group .badge {
  text-decoration: none;
  padding: 5px 10px;
  border-radius: 50px;
  border: 2px dashed var(--primary);
  border-style: double;
  color: #ffffff;
  font-size: 14px;
  margin-left: 10px;
  margin-bottom: 10px;
  display: inline-block;
  cursor: pointer;
}
.badges-group .badge:hover {
  background-color: var(--primary);
  color: white;
}

.link {
  color: var(--primary);
  text-decoration: none;
}
.link:hover {
  text-decoration: underline;
}

.list-simple {
  list-style: circle;
  list-style-position: inside;
  color: var(--muted);
  margin: 20px 0;
  padding: 0;
}
.list-simple li {
  font-weight: 400;
  font-size: clamp(16px, 80vw, 18px);
  line-height: clamp(22px, 80vw, 28px);
  color: var(--muted);
  margin-bottom: 15px;
}
.list-simple li b {
  font-weight: 600;
}

.list {
  margin: 0;
  margin-top: 10px;
  margin-bottom: 10px;
  font-size: clamp(16px, 80vw, 18px);
}
.list .list-item {
  font-weight: 400;
  font-size: clamp(16px, 80vw, 18px);
  line-height: clamp(22px, 80vw, 28px);
  color: var(--muted);
}
.list .list-item .list-link {
  display: inline-flex;
  align-items: center;
  text-decoration: none;
  color: var(--text);
}
